.slideshow {
  display: grid;
  place-items: start;
}

.slideshow__slide {
  grid-column: 1/2;
  grid-row: 1/2;
  opacity: 0;
  transition: all var(--t-medium);
  z-index: -1;
}

.slideshow__slide[data-status=active] {
  opacity: 1;
  z-index: 1;
}

.slideshow__controls {
  align-items: center;
  display: flex;
  grid-column: 1/2;
  grid-row: 1/2;
  height: 100%;
  justify-content: space-between;
  padding-inline: 1.5em;
  width: 100%;
  z-index: 2;
}

.slideshow__prev,
.slideshow__next {
  background-color: rgba(255, 255, 255, 0.5);
  border: 0;
  cursor: pointer;
  font-size: 0;
  height: 50px;
  width: 50px;
}
.slideshow__prev:before,
.slideshow__next:before {
  font-family: "icons";
  font-size: var(--fs-800);
}

.slideshow__prev:before {
  content: "\e804";
}

.slideshow__next:before {
  content: "\e807";
}

.slideshow__dot {
  height: 1em;
  width: 1em;
}

.slideshow__dot[data-status=active] {
  background-color: red;
}

.slideshow--circular img,
.slideshow--round img {
  border-radius: 100%;
  border: clamp(0.25em, 2vw, 1em) solid var(--c-grey-100);
}

.slideshow--rounded img {
  border-radius: 2em;
  border: clamp(0.25em, 2vw, 1em) solid var(--c-grey-100);
}

.slideshow--center .slideshow__slide {
  margin-inline: auto;
}